.app{
  height: 100%;
  background: linear-gradient(#fffef8, #fff9bb);
}
.logo_img{
  width: 100%;
  padding: 0.3rem;
  text-align: center;
}
.logo_img img{
  width: 18rem;
  height: 6 rem;
}
.section_2{
  margin: 1px;
  display: flex;
  word-wrap: break-word;
  justify-content: center;
  flex-wrap: wrap;
  align-self: center;
  align-content: center;
  align-items: center;
}
.award_img{
  width: max-content;
  text-align: center;
}
.award_img img{
  width: 20rem;
  height: 45rem;
}
.Section_2_content{
  width: 60%;
  display: flex;
  flex-direction: column;
}
.Section_2_content img{
  width: 100%;
  height: 32rem;
  object-fit: fill;
}
.sectioon_2_p1{
  font-weight: 600;
  font-size: large;
}
.Section_2_content ul{
  word-wrap: break-word;
  padding: 1rem 2rem;
  font-weight: 500;
}
.section_3{
  margin-top: 1rem;
  text-align: center;
}
.section_3 img{
  margin-top: 10px;
  width: 90%;
  height: 50%;
  padding: 1rem 1rem;
}
.line{
  height: 2px;
  margin: 1rem;
  background-color: #C92B2F;
}
.below_hr{
  text-align: center;
  font-weight: 700;
  font-size: large;
}
.last_line{
  margin-top: 1rem;
  text-align: center;
  font-size: large;
  font-weight: 500;
}
.last_line span{
  border-left: 2px solid #C92B2F;
  height: fit-content;
}
.footer{
  height: 12rem;
  background: #C92B2F;
  margin: 1rem 1rem;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  font-weight: 500;
  font-size: large;
  color: white;
}
.footer_icon{
  font-size: x-large;
  font-weight: bolder;
  color: black;
  background-color: white;
  border-radius: 50px;
  margin: 1rem;
}
.footer div{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

@media only screen and (max-width: 600px) {
  .Section_2_content img{
    width: 100%;
    height: 10rem;
    object-fit: fill;
  }
}